$logo-font-path: "/assets/fonts/font-logo/";
@font-face {
  font-family: 'logo';
  src:  url('#{$logo-font-path}/logo.eot?y5gz8h');
  src:  url('#{$logo-font-path}/logo.eot?y5gz8h#iefix') format('embedded-opentype'),
  url('#{$logo-font-path}/logo.ttf?y5gz8h') format('truetype'),
  url('#{$logo-font-path}/logo.woff?y5gz8h') format('woff'),
  url('#{$logo-font-path}/logo.svg?y5gz8h#logo') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'logo' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-M:before {
  content: "\e900";
}
.icon-M{color: #021389;}


body{background: #F0F2F5;font-family: NotoSansHans-Regular,AvenirNext-Regular,arial,Hiragino Sans GB,"Microsoft Yahei","Hiragino Sans GB","WenQuanYi Micro Hei",sans-serif;}
footer{background: #eee;padding-top: 10px;}
.navbar-brand{line-height: 50px; padding: 0;  font-size: 25px;  color: #021389;}

a,a:hover{color:#333;}

.navbar{}
.jumbotron{padding-bottom: 0;padding-top: 0;}
.jumbotron h1{font-size: 3rem;}
.sec-header{margin-top: 0;}
.sec-header h2{font-size: 2.5rem;}



.meidiaList:last-child .media{border-bottom: 0;}
.thumbnail{height: 230px;overflow: hidden; border-radius: 0;  box-shadow: 0 1px 5px #c3c3c3; padding: 0;}
.thumbnail .lazy{width: 300px;max-height: 150px;}
.caption h4{text-align: center;white-space: nowrap;text-overflow: ellipsis;overflow: hidden;}
.caption p{max-height: 40px;overflow: hidden;text-overflow: ellipsis;}

.media{border-bottom: 1px dashed #ddd;}
.meidiaList .media{margin: 0 3rem;}
.media:first-child{margin-top: 10px;}
.media-left .lazy{width: 100px;}
.side-meida{padding-bottom: .6rem;}
.side-meida .media-object{width: 64px;height: 64px;}
.side-meida h4{font-size: 14px;}
.fa{padding: 0 5px;}
.tags .btn{margin: .5rem;}

.panel-article{max-width: 978px;margin: 0 auto;padding: 0 5rem;}
.article-mate{font-size: .8em;margin-bottom: 15px;}
.alticle{margin-bottom: 10px;line-height: 180%;box-sizing:border-box;font-size: 1.2em;}
.alticle h1{font-size: 28px;margin: 1em auto;}
.alticle p{margin-left: .5rem;}




